radas.")])])]),_c('div',{attrs:{"titulo":"Etapa de síntesis o producción"}},[_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Últimas en entrar, primeras en salir (UEPS)"}},[_c('div',{staticClass:"col-sm-2 mb-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/30.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-9 mb-4 mb-md-0"},[_c('p',[_vm._v("Su mecánica es opuesta al anterior, puesto que la materia prima a utilizar será la última comprada, esto lleva a que los saldos del inventario final se compongan por la materia prima adquirida con mayor antigüedad, esto puede generar desfases en los precios y llevar a un sesgo en la interpretación de la información.")])])])]),_c('div',{attrs:{"titulo":"Etapa de acondicionamiento o procesamiento"}},[_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Promedio ponderado"}},[_c('div',{staticClass:"col-sm-2 mb-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/31.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-9 mb-4 mb-md-0"},[_c('p',[_vm._v("El sistema funciona afectando el costo de la materia prima en función de todas las compras realizadas en el tiempo. Esto significa que las variaciones de los precios ajustan el valor promedio de todas las existencias disponibles en el inventario.")])])])])])],1)]),_vm._m(48),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-10"},[_c('div',{staticClass:"tarjeta p-3 mb-5",staticStyle:{"background-color":"#FFFAD0"},attrs:{"data-aos":"flip-up"}},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_vm._m(49),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(50),_c('div',{staticClass:"col-sm-auto"},[_c('a',{staticClass:"boton color-acento-contenido texto-blanco",attrs:{"href":_vm.obtenerLink('/downloads/