os costos indirectos de fabricación aporta más complejidad al cálculo del costo de producción de lo que se podría pensar, lo anterior debido a que existen limitaciones significativas para determinar la porción de un costo indirecto a una unidad producida. Por ejemplo, si se debe reparar una máquina de ordeño, en cuánto se debe incrementar el costo de producción de cada litro de leche.")]),_vm._m(72),_vm._m(73),_vm._m(74),_vm._m(75),_vm._m(76),_vm._m(77),_vm._m(78),_vm._m(79),_vm._m(80),_vm._m(81),_vm._m(82),_c('separador'),_vm._m(83),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Las órdenes de producción, también conocidas como órdenes de pedido, suelen ser utilizadas en empresas en las que la línea de producción no opera de manera permanente. Bajo este sistema, la determinación del coste de un lote de producto terminado se estima de forma independiente a otros, por lo que pueden existir variaciones en el costo de venta unitario entre una y otra orden.")]),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Este sistema ofrece una gran utilidad en unidades productivas que trabajan por encargo, puesto que las mediciones varían en función de la situación actual. El siguiente formato propone un esquema para utilizar bajo esta metodología; su diligenciamiento suele ser precedido por una solicitud comercial, es decir, el área de ventas indica la unidad de productos vendidos, y de cara a esto, el departamento de producción determina el costo unitario de cara a las exigencias presentadas, ver tabla.")]),_vm._m(84),_vm._m(85),_c('h3',{staticClass:"titulo-tercero mt-5"},[_vm._v("2.7.1 Características")]),_vm._m(86),_vm._m(87),_c('h3',{staticClass:"titulo-tercero mt-5"},[_vm._v("2.7.2. Diligenciamiento")]),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Al diligenciar la orden de producción, se espera que los insumos estimados sean los suficientes para dar cumplimiento con la demanda del cliente. Gracias a esto, se minimizan los costos de almacenaje, caducidad u obsolescenci